import { useEffect, useState } from 'react';
import { DataGrid } from '@mui/x-data-grid';

const DataTable = ({setSelected, data}) => {

  const [rows, setRows] = useState([]);
  
  const columns = [
    { field: 'id', headerName: '', flex: 1 },
    { field: 'workCode', headerName: '', flex: 2 },
    { field: 'workName', headerName: '', flex: 3 },
    { field: 'company', headerName: '', flex: 2 },
    { field: 'empName', headerName: '', flex: 2 },
    { field: 'position', headerName: '', flex: 1 },
    { field: 'task', headerName: '', flex: 4, sortable: false },
    { field: 'telephone', headerName: '', flex: 2, sortable: false },
  ];

  useEffect(() => {
    setRows([]);

    data.map(({empId, workCode, workName, company, empName, position, task, telephone}) => {
      setRows((prevState) => [
        ...prevState,
        {
          id: empId,
          workCode: workCode,
          workName: workName,
          company: company,
          empName: empName,
          position: position,
          task: task,
          telephone: telephone
        }]
      );
    });

  }, [data]);

  return (
    <div style={{ height: 530, width: '70%' }}>
      <DataGrid
        getRowId={(row) => row.id}
        rows={rows}
        columns={columns}
        pageSize={8}
        rowsPerPageOptions={[10]}
        checkboxSelection
        onSelectionModelChange={(selected) => {
          setSelected(selected);
        }}
      />
    </div>
  );
}

export default DataTable;